h1 {
  font-size: var(--font-size-XL);
}

h2 {
  font-size: calc(var(--font-size-XL) * 0.8);
}

h1,
h2 {
  margin: calc(var(--unit) / 2) 0 var(--unit) 0;
}

p {
  line-height: calc(var(--font-size-regular) * 1.5);
  margin-bottom: calc(var(--unit) * 2);
}

li,
p {
  font-size: var(--font-size-regular);
}

li {
  line-height: calc(var(--unit) * 1.8);
  margin-bottom: calc(var(--unit) / 2);
  margin-left: calc(var(--unit) * 3);
}

small {
  font-size: var(--font-size-S);
}

blockquote {
  background-color: var(--color-base-light);
  border-radius: var(--border-radius);
  font-size: var(--font-size-regular);
  line-height: calc(var(--unit) * 1.8);
  margin: calc(var(--unit) * 2) 0;
  padding: calc(var(--unit) * 2);
}

blockquote > * {
  display: block;
}

button {
  align-items: center;
  background-color: transparent;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  padding: var(--unit) calc(var(--unit) * 1.5);
}

section {
  padding-bottom: var(--offset);
  padding-top: var(--offset);
}

button.button {
  background-color: transparent;
  border-radius: 0.2rem;
  font-size: 1.1rem;
  font-weight: 700;
  padding: var(--unit) calc(var(--unit) * 1.5);
}

button.button.contrast {
  border: 1px solid var(--color-text);
  color: var(--color-text);
}

button.button.contrast:hover {
  background-color: hsla(0, 0%, 100%, 0.2);
}

header {
  padding-bottom: calc(var(--unit) * 2);
  padding-top: calc(var(--unit) * 2);
}

header .logo {
  font-size: 2.5rem;
}

header nav {
  align-self: center;
  flex: 1;
  margin-left: var(--unit);
}

header nav a {
  color: var(--color-text-light);
  margin-left: calc(var(--offset) / 2);
  text-decoration: none;
}

header nav a:hover {
  color: var(--color-text);
}

header nav a > strong {
  background-color: var(--color-accent);
  border-radius: var(--border-radius);
  color: var(--color-primary);
  font-size: 0.8rem;
  padding: 0.2rem 0.4rem;
}

header nav a:hover > strong {
  color: var(--color-text);
}

section.text {
  background-color: var(--color-text);
  color: var(--color-text-contrast);
}

section.expanded {
  padding-bottom: calc(var(--offset) * 2);
  padding-top: calc(var(--offset) * 2);
}

nav.learn {
  background-color: var(--color-base-light);
  border-radius: var(--border-radius);
  max-width: 300px;
  padding: calc(var(--unit) * 2);
  position: sticky;
  top: calc(var(--offset) * 1);
}

nav.learn > a {
  color: var(--color-text-light);
  display: block;
  line-height: 2rem;
  text-decoration: none;
}

nav.learn > a:hover {
  color: var(--color-text-contrast);
}

.row {
  align-items: baseline;
  display: flex;
  flex-direction: row;
}

.currency {
  font-family: var(--font-family-currency);
  font-size: var(--font-size-L);
  font-weight: 700;
}

.content {
  display: flex;
  margin: 0 auto;
  max-width: 1368px;
  padding-left: var(--offset);
  padding-right: var(--offset);
}

.content .col {
  flex: 1;
}

.content .col:first-child {
  flex: 2;
  padding-right: var(--offset);
}

.content .col + .col:last-child {
  padding-left: var(--offset);
}

.info {
  color: var(--color-text-light);
  margin: 0 calc(var(--unit) / 2);
}

@media only screen and (max-width: 1024px) {
  .content {
    flex-direction: column;
  }

  .content .col {
    margin-bottom: calc(var(--offset) * 2);
    padding-right: 0;
  }

  .content .col:first-child {
    max-width: 100vw;
    padding-right: 0;
  }

  .content .col + .col:last-child {
    padding-left: 0;
  }

  .row {
    background-color: blue;
  }
}
